 import {Injectable} from '@angular/core';
 import {HttpClient,HttpHeaders} from '@angular/common/http';
 import 'rxjs/Rx';

 @Injectable()
 export class  MovieService{
apikey:string;
    constructor(private _http:HttpClient){
        this.apikey='4d29b35a33734de1e1f8e9200569f1bf';
        console.log('initialised yo');
    }

    getPopular(){
        return this._http.get('https://api.themoviedb.org/3/discover/movie?sort_by=popularity.desc&api_key=4d29b35a33734de1e1f8e9200569f1bf')
            .map((res:Response) => res);
    }
    getInTheaters(){
        return this._http.get('https://api.themoviedb.org/3/discover/movie?primary_release_date.gte=2016-09-26&primary_release_date.lte=2016-10-30&api_key='+this.apikey)
            .map((res:Response) => res);
    }
    searchMovies(searchStr:string){
        return this._http.get('https://api.themoviedb.org/3/search/movie?query='+searchStr+'&sort_by=popularity.desc&api_key='+this.apikey)
            .map((res:Response) => res);
    }
        
    getMovie(id:string){
        return this._http.get('https://api.themoviedb.org/3/movie/'+id+'?api_key='+this.apikey)
            .map((res:Response) => res);
    }

 }
